.RepoBox__Root {
  border-radius: .2rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-bottom: 1rem;
  background: var(--bgcolor_1);
}
.RepoBox__Title {
  text-align: left;
  /* horizontal aligment*/
  display:flex;
  justify-content: left;
  align-items: center;
  margin-bottom: .6rem;
  margin-right: .4rem;

}
.repoBox__titleIcon {
  background: rgb(36, 36, 36);
  margin: .4rem 0 0 .4rem;
  border-radius: .4rem;
}

.RepoBox__TitleImg {
  width:  4rem;
  height: 4rem;
}
.RepoBox__TitleText {
  margin-left: 1rem;
  font-size: 1.4rem;
  color: rgb(151, 151, 151);
}
.RepoBox__TitleText > a {
  color: rgb(241, 241, 241);
  text-decoration: none;
}


.RepoBox__description {
  border-color: rgb(46, 0, 0);
  text-align: left;
  /*padding: .1px .4rem;*/
  border-radius: .4rem;
  margin: 0 .4rem;
  background: rgb(36, 36, 36);
  padding: .2rem .4rem;
  margin-bottom: .1rem;
}
.RepoBox__description > p {
  color: rgb(146, 145, 145);
}

.RepoBox__Footer {
  width: 100%;
  display:flex;
  justify-content: right;
  align-items: center;
}

.RepoBox__Info {
  display:flex;
  justify-content: left;
  align-items: center;
  margin-left: .4rem;

}

.RepoBox__Info > p {
  color: rgb(187, 187, 187);
}
.RepoBox__Info > img {

  padding:  4px;

  background: rgb(32, 28, 28);
  margin-left: .4rem;
  border-radius: 4px;
  width: 2.8rem;
  height: 20pt;
}

.RepoBox__IconContainer {
  /* 
  background: peru;
  */
  margin: 0 .4rem;
  text-align: right;
}

.RepoBox__Icon {
  justify-content: center;
  /*
  */
  padding: 4px;
  background: var(--bg-box-icons);
  margin-left: .4rem;
  border-radius: 4px;
  width:  20pt;
  height: 20pt;
}
