@import url('https://fonts.googleapis.com/css2?family=Assistant:wght@400&display=swap');

:root {

  --forecolor_0: rgb(206, 206, 206);/*[scroll ]*/
  --forecolor_1: rgb(206, 206, 206);
  --forecolor_2: rgb(206, 206, 206);
  --forecolor_3: rgb(206, 206, 206);

  --bgcolor_0: rgb(43, 40, 44);/*[ body, scroll ]*/
  --bgcolor_1: rgb(61, 60, 61); /*[ searchBox, repoBox ]*/
  --bgcolor_2: rgb(43, 40, 44);
  --bgcolor_3: rgb(43, 40, 44);
  --bgcolor_4: rgb(43, 40, 44);
  /*
  --bgcolor_5: rgb(43, 40, 44);
  --bgcolor_6: rgb(43, 40, 44);
  --bgcolor_7: rgb(43, 40, 44);
  --bgcolor_8: rgb(43, 40, 44);
  */



  --bgc-description: rgba(136, 136, 136, 0.534);
  --bg-box-icons: rgba(104, 95, 95, 0.671);
  --color-title: rgb(57, 3, 82);

  --bg-search-box: rgb(238, 238, 238);
  --bg-repo-box: rgb(238, 238, 238);
}

body {
  margin: 0;
  font-family: 'Assistant', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--bgcolor_0);
}
::-webkit-scrollbar {
  background: var(--bgcolor_0);
  width: 10pt;
}
::-webkit-scrollbar-thumb {
  background: var(--forecolor_0);
}

